.montyhall {
  display: grid;
  padding: 2em;
  grid-template-areas: "heading" "slider" "interactive";
  grid-template-rows: 1fr
    400px
    400px;
}
.montyhall-heading {
  font-family: "Rubik Mono One", sans-serif;
  font-size: 2.5rem;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  border-width: 0 0 1px 0;
  border-style: solid;
  border-color: lightgray;
  grid-area: heading;
  -ms-flex-pack: center;
  justify-content: center;
}
.montyhall #montyhall-slider {
  height: 100%;
  text-align: justify;
  border-width: 0 0 1px 0;
  border-style: solid;
  border-color: lightgray;
  grid-area: slider;
}
.montyhall #montyhall-slider .slider-content {
  padding: 5%;
}
.montyhall #montyhall-slider .slider-content h2 {
  white-space: pre;
}
.montyhall-interactive {
  display: grid;
  grid-area: interactive;
  grid-template-areas: "scoreboard" "doors" "instructions";
  grid-template-rows: auto
      1fr
      1fr;
}
.montyhall-interactive-scoreboard {
  display: grid;
  padding: 5%;
  -ms-flex-align: center;
  align-items: center;
}
.montyhall-interactive-stage {
  display: grid;
  grid-template-areas: "doors controls";
  grid-template-columns: 1fr 48px;
}
.montyhall-interactive-stage-doors {
  display: grid;
  margin: 5%;
  grid-area: doors;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 5%;
}
.montyhall-interactive-stage-doors .door-select {
  display: -ms-flexbox;
  display: flex;
  border-width: 1px;
  border-style: solid;
  border-color: lightgray;
  -ms-flex-pack: center;
  justify-content: center;
}
.montyhall-interactive-stage-doors .door-select#highlight {
  border-color: black;
}
.montyhall-interactive-stage-doors .door-select#hover {
  -webkit-transform: scale(1.05, 1.05);
  -ms-transform: scale(1.05, 1.05);
  transform: scale(1.05, 1.05);
}
.montyhall-interactive-stage-doors .door-select[disabled] {
  cursor: auto;
  -webkit-transform: scale(1, 1);
  -ms-transform: scale(1, 1);
  transform: scale(1, 1);
  background-color: whitesmoke;
}
.montyhall-interactive-stage-controls {
  display: grid;
  grid-area: controls;
  grid-template-areas: "add" "count" "reduce";
  grid-template-rows: 48px
          1fr
          48px;
  align-self: center;
}
.montyhall-interactive-stage-controls #control-add {
  grid-area: add;
}
.montyhall-interactive-stage-controls h3 {
  text-align: center;
  grid-area: count;
}
.montyhall-interactive-stage-controls #control-reduce {
  grid-area: reduce;
}
.montyhall-interactive-instructions {
  display: grid;
  text-align: center;
  grid-template-rows: 1fr 1fr;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.montyhall-interactive-instructions h2 {
  font-family: "Rubik Mono One", sans-serif;
  font-weight: normal;
}
.montyhall-interactive-instructions #instruction-stickswitch {
  margin: 2.5%;
}
.montyhall-interactive-instructions #instruction-select {
  border-width: 1px;
  border-style: solid;
  border-color: lightgray;
}
@media screen and (min-width: 800px) {
  .montyhall {
    grid-template-areas: "heading heading" "slider interactive";
    grid-template-rows: 1fr
      400px;
    grid-template-columns: 1fr 1fr;
  }
  .montyhall #montyhall-slider {
    border-width: 0 1px 0 0;
  }
  .montyhall-interactive-scoreboard {
    border-width: 0 0 1px 0;
    border-style: solid;
    border-color: lightgray;
  }
}



